import React, { useMemo } from "react";
import ReusableTable from "../Table/Table";
import { Alert } from "react-bootstrap";

const ErrorPopup = ({ errors }) => {
  // Format errors into table data
  const errorData = useMemo(
    () =>
      errors.map((error, index) => {
        const rowMatch = error.match(/Row (\d+):/);
        const rowNum = rowMatch ? rowMatch[1] : "-";
        const columnMatch = error.match(/"([^"]*)" is required/);
        const columnName = columnMatch ? columnMatch[1] : "-";
        const errorMsg = rowMatch ? error.split(": ")[1] : error;

        return {
          id: index,
          rowNumber: rowNum,
          errorDescription: errorMsg,
          columnName: columnName,
        };
      }),
    [errors]
  );

  // Define table columns
  const columns = useMemo(
    () => [
      {
        accessorKey: "rowNumber",
        header: "Row Number",
      },
      {
        accessorKey: "errorDescription",
        header: "Error Description",
      },
      {
        accessorKey: "columnName",
        header: "Column Name",
      },
    ],
    []
  );

  return (
    <>
      <Alert variant="info" className="text-center text-sm-start mb-3">
        <strong>Note:</strong> Below reports show only uploaded spreadsheet
        error rows. To resolve below issues, do the required changes in the
        spreadsheet and re-import.
      </Alert>
      <ReusableTable columns={columns} data={errorData} />
    </>
  );
};

export default ErrorPopup;
